<template>
  <div class="all">
    <div class="header">
      <div class="pull-left logo po" @click="toMajorPage">
        <img class="logo-img" src="../../static/img/logo.png" alt="grandbox">
        <img class="pull-right" src="../../static/img/header-2.png"/>
      </div>
      <div class="pull-left">
        <div class="font-content">
          <img class="cloud" src="../../static/img/header-1.png" alt="">
          <span class="header-font">On-boarding</span>
          <img src="../../static/img/header-2.png"/>
        </div>
      </div>
      <div class="pull-right po" @click="toPage(0)">
        <img src="../../static/img/header-2.png"/>
        <img class="register-img" src="../../static/img/header-12.png"/>
        <span class="register-span"></span>
      </div>
      <div class="pull-right po" @click="toPage(1)">
        <img src="../../static/img/header-2.png"/>
        <img class="register-img" src="../../static/img/header-11.png"/>
        <span class="register-span"></span>
      </div>
      <div class="pull-right po" @click="login">
        <img src="../../static/img/header-2.png"/>
        <img class="register-img" src="../../static/img/header-10.png"/>
        <span class="register-span"></span>
      </div>
    </div>
    <div class="content">
      <div class="content-right">
        <div class="row">
          <div class="col-sm-11">
            <img class="login-logo" src="../../static/img/login-1.png" alt="">

            <div class="login-content">
              <label class="block">
                <p>:</p>
                <input type="text" class="form-control" v-model="inputName">
              </label>
              <label class="block">
                <p>:</p>
                <input @keyup.enter="login" class="form-control" type="password" v-model="inputPassword">
              </label>
              <!--<img @click="login" class="my-btn login-img po" src="../../static/img/login-2.png" alt="">-->
              <span class="my-btn" @click="login"><img src="../../static/img/red-login.png" alt=""></span>

              <a href="javascript:void(0)" class="forget-p" @click="showPM"></a>

            </div>

            <div class="alert">
              <i class="alert-img inline"></i>
              <span class="alert-title"></span>
              <div class="alert-content">
                <span class="block">
                  grandboxAnalyzeManagementBioMedDBgrandbox
                  
                </span>
                <span class="block">
                  grandboxgrandbox
                  
                </span>
                <span class="block">
                  grandbox
                  
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <forgetP></forgetP>

  </div>
</template>

<script>
  import Vue from 'vue'
  import forgetP from './global/forgetPassword.vue'

  export default {
    components: {
      'forgetP': forgetP,
    },
    data: function () {
      return {
        inputName: '',
        inputPassword: ''
      }
    },
    created: function () {
      if (localStorage.token) {
//        this.$router.push({path: '/myData'})
      }
    },
    methods: {
      toPage: function (type) {
        if (type) { //
          window.location.href = this.helpHtml;
        } else { //
          window.open(this.feedBackHtml)
        }
      },
      login: function () {
        const _vue = this;
        this.loginAxios({
          url: 'auth/login',
          method: 'post',
          data: {
            username: this.inputName,
            password: this.inputPassword
          }
        }).then(function (resp) {
          localStorage.token = 'Bearer '+resp.data.token;
          let data = JSON.parse(_vue.Base64.decode(resp.data.token.split('.')[1]));
          localStorage.grandId = data.sub;
          localStorage.grandGroup = data.group;
          localStorage.isAd = data.is_admin;
          localStorage.uname = data.name;
          localStorage.time = data.exp;


          //
          const nextPath = _vue.$route.query.next ? _vue.$route.query.next : '/myData';
          let queryStr =_vue.toLoginParam;
          let arr = queryStr.split('&');
          let queryObj= {};
          $.each(arr,function (key,val) {
            let a = val.substring(0,val.indexOf('='));
            let b = val.substring(val.indexOf('=')+1,val.length);
            queryObj[a] = b
          });
          _vue.$router.push({path: nextPath,query:queryObj});

          /*group 1 2 3   */
        }).catch(function (error) {
          _vue.catchFun(error);
        });
      },
      clickLi: function (event) {
        $(".content-left").find(".active").removeClass('active');
        $(event.target).closest('li').addClass('active')
      },
      toMajorPage: function () {
        this.$router.push({path: '/'})
      },
      showPM:function () {
        $("#forgetModal").modal("show")
      }
    }
  }
</script>
<style lang="less" scoped>
  @border: rgb(168, 185, 209);
  @in: rgb(14, 125, 195);
  @triangle-color: rgb(0, 118, 192);
  @font-color: rgb(21, 65, 65);
  .all {
    height: 100%;
    min-width: 1300px;
    .header {
      height: 50px;
      line-height: 48px;
      width: 100%;
      color: #fff;
      background: url('../../static/img/header-bc.png') no-repeat center;
      background-size: 100%;
      border-bottom: 1px solid rgb(0, 77, 144);
      .logo {
        width: 251px;
        text-align: center;
      }
      .font-content {
        .cloud {
          margin: 0 6px 0 32px;
        }
        .header-font {
          margin-right: 28px;
        }
      }
      .register-img {
        margin: 0 5px 2px 20px;
      }
      .register-span {
        margin-right: 20px;
      }
    }
    .content {
      border-top: 1px solid @border;
      height: calc(~'100vh - 52px');
      float: left;
      margin-top: -1px;
      .forget-p{
        font-size: 12px;
        vertical-align: bottom;
        margin-left: 20px;
      }
      .content-left {
        width: 250px;
        height: 100%;
        ul {
          margin-left: -40px;
          .triangle {
            float: right;
            width: 0;
            height: 0;
            margin-top: 4px;
            border-top: 15px solid transparent;
            border-right: 18px solid @triangle-color;
            border-bottom: 15px solid transparent;
            display: none;
          }
          .active {
            .triangle {
              display: block;
            }
            border-right: 1px solid @triangle-color;
            color: @in;
          }
          li {
            list-style: none;
            height: 40px;
            line-height: 40px;
            border-bottom: 1px solid @border;
            font-weight: bold;
            &:hover {
              color: @in;
            }
            .img-analyze {
              width: 16px;
              height: 18px;
              display: inline-block;
              background: url('../../static/img/all-1.png');
              margin: 0 4px -4px 11px;
            }
            .img-manage {
              width: 16px;
              height: 18px;
              display: inline-block;
              background: url('../../static/img/all-1.png') 0 453px;
              margin: 0 4px -4px 11px;
            }
            .img-data {
              width: 16px;
              height: 22px;
              display: inline-block;
              background: url('../../static/img/all-1.png') 0 428px;
              margin: 0 4px -4px 11px;
            }
          }
          .li-analyze {
            &:hover {
              .img-analyze {
                background-position-x: 16px;
              }
            }
          }
          .li-manage {
            &:hover {
              .img-manage {
                background-position-x: 16px;
              }
            }
          }
          .li-data {
            &:hover {
              .img-data {
                background-position-x: 16px;
              }
            }
          }
        }
      }
      .content-right {
        border-left: 1px solid @border;
        float: left;
        min-height: 100%;
        padding-left: 32px;
        width: calc(~'100vw - 0px');
        min-width: 1050px;
        .login-logo {
          margin-top: 40px;
          width: 100%;
        }
        .login-content {
          color: @font-color;
          margin: 5vh 0 7vh 0;
          .block {
            margin: 20px 0;
            input {
              width: 20vw;
            }
          }
          .login-img {
            margin-top: 10px;
          }
        }
        .alert {
          width: 100%;
          padding: 25px 20px;
          background-color: rgb(237, 246, 251);
          .alert-img {
            width: 15px;
            height: 15px;
            background: url('../../static/img/all-2.png') 0 15px;
          }
          .alert-title {
            color: rgb(21, 65, 65);
            font-weight: bold;
          }
          .alert-content {
            color: rgb(49, 49, 49);
            font-weight: bold;
            margin-top: 12px;
          }
        }
      }
    }
  }
</style>



